.App {
  text-align: center;
  user-select: none;
  height: 100vh;
  overflow: hidden;
}

/* Canvas Page Layout */
.canvas-page {
  display: flex;
  flex-direction: column;
  height: 100vh;
  background-color: #282c34;
  color: white;
}

.canvas-header {
  background-color: #1e1e1e;
  padding: 15px 20px;
  border-bottom: 2px solid #444;
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.canvas-header h1 {
  margin: 0;
  color: #61dafb;
  font-size: 24px;
}

.canvas-main {
  display: flex;
  flex: 1;
  overflow: hidden;
}

/* Sidebar */
.sidebar {
  width: 280px;
  background-color: #1e1e1e;
  border-right: 2px solid #444;
  padding: 20px;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.sidebar h3 {
  margin: 0 0 10px 0;
  color: #61dafb;
  font-size: 16px;
}

/* Sprite Selector */
.sprite-selector {
  background-color: #2a2a2a;
  padding: 15px;
  border-radius: 6px;
}

.sprite-list {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.sprite-option {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 8px;
  background-color: #333;
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.2s;
  border: 2px solid transparent;
}

.sprite-option:hover {
  background-color: #404040;
}

.sprite-option.selected {
  border-color: #61dafb;
  background-color: #1a4a5c;
}

.sprite-option img {
  width: 32px;
  height: 32px;
  image-rendering: pixelated;
  background-color: #fff;
  border-radius: 2px;
}

.sprite-option span {
  font-size: 14px;
  color: #ccc;
}

/* Fish Inspector */
.fish-inspector {
  background-color: #2a2a2a;
  padding: 15px;
  border-radius: 6px;
}

.inspector-content p {
  margin: 5px 0;
  font-size: 14px;
  color: #ccc;
}

.delete-fish-btn {
  background-color: #ff6b6b;
  color: white;
  border: none;
  padding: 8px 12px;
  border-radius: 4px;
  cursor: pointer;
  font-weight: bold;
  margin-top: 10px;
  transition: all 0.2s;
}

.delete-fish-btn:hover {
  background-color: #ff5252;
}

/* Instructions */
.instructions {
  background-color: #2a2a2a;
  padding: 15px;
  border-radius: 6px;
}

.instructions ul {
  text-align: left;
  margin: 0;
  padding-left: 20px;
}

.instructions li {
  margin: 5px 0;
  font-size: 14px;
  color: #ccc;
}

/* Page Tabs */
.page-tabs {
  background-color: #2a2a2a;
  padding: 10px;
  border-radius: 6px;
}

.tabs-list {
  display: flex;
  gap: 10px;
  align-items: center;
  flex-wrap: wrap;
}

.page-tab {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 8px 12px;
  background-color: #333;
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.2s;
  border: 2px solid transparent;
  min-width: 120px;
  position: relative;
}

.page-tab:hover {
  background-color: #404040;
}

.page-tab.active {
  border-color: #61dafb;
  background-color: #1a4a5c;
}

.page-tab.editing {
  background-color: #2a4a2a;
}

.page-name {
  flex: 1;
  font-size: 14px;
  color: #ccc;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

.page-name-input {
  background-color: transparent;
  border: 1px solid #61dafb;
  color: white;
  padding: 2px 4px;
  border-radius: 2px;
  font-size: 14px;
  width: 100%;
}

.page-actions {
  display: flex;
  gap: 4px;
}

.edit-btn, .remove-btn {
  background: none;
  border: none;
  cursor: pointer;
  padding: 2px 4px;
  border-radius: 2px;
  font-size: 12px;
  transition: all 0.2s;
}

.edit-btn:hover {
  background-color: #61dafb;
}

.remove-btn:hover {
  background-color: #ff6b6b;
}

.fish-count {
  font-size: 12px;
  background-color: #61dafb;
  color: #282c34;
  padding: 2px 6px;
  border-radius: 10px;
  font-weight: bold;
}

.add-page-btn {
  padding: 8px 12px;
  background-color: #61dafb;
  color: #282c34;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-weight: bold;
  transition: all 0.2s;
  font-size: 14px;
}

.add-page-btn:hover {
  background-color: #4fa8c5;
  transform: translateY(-1px);
}

/* Controls */
.controls {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 15px;
  flex-wrap: wrap;
}

.controls button {
  padding: 8px 16px;
  background-color: #61dafb;
  color: #282c34;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-weight: bold;
  transition: all 0.2s;
}

.controls button:hover:not(:disabled) {
  background-color: #4fa8c5;
  transform: translateY(-1px);
}

.controls button:disabled {
  background-color: #444;
  color: #888;
  cursor: not-allowed;
}

.zoom-indicator {
  font-weight: bold;
  color: #61dafb;
  padding: 8px 16px;
  background-color: #333;
  border-radius: 4px;
  min-width: 80px;
}

.reset-btn {
  background-color: #ff6b6b !important;
}

.reset-btn:hover:not(:disabled) {
  background-color: #ff5252 !important;
}

/* Viewport Container */
.viewport-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

/* Interactive Viewport */
.viewport {
  flex: 1;
  background-color: #1a1a1a;
  border-radius: 8px;
  overflow: hidden;
  position: relative;
  margin: 20px;
  border: 2px solid #444;
}

.world {
  position: relative;
  width: 100%;
  height: 100%;
  image-rendering: pixelated;
  transition: transform 0.1s ease-out;
}

/* Grid Background */
.grid-background {
  position: absolute;
  top: -2000px;
  left: -2000px;
  width: 4000px;
  height: 4000px;
  background-image: 
    linear-gradient(rgba(255, 255, 255, 0.1) 1px, transparent 1px),
    linear-gradient(90deg, rgba(255, 255, 255, 0.1) 1px, transparent 1px);
  background-size: 32px 32px;
  pointer-events: none;
  z-index: 0;
}

/* Fish Resource Component */
.fish-resource {
  position: absolute;
  cursor: pointer;
  z-index: 10;
  transition: all 0.1s;
}

.fish-resource:hover {
  filter: brightness(1.2);
}

.fish-resource.selected {
  filter: brightness(1.3) drop-shadow(0 0 8px #61dafb);
}

.fish-resource .sprite {
  image-rendering: pixelated;
  -ms-interpolation-mode: nearest-neighbor;
  object-fit: none;
  object-position: top left;
  pointer-events: none;
  user-select: none;
  transform-style: preserve-3d;
  backface-visibility: hidden;
}

/* Health Bar */
.health-bar {
  position: absolute;
  top: -6px;
  left: 0;
  width: 100%;
  height: 4px;
  background-color: rgba(255, 0, 0, 0.3);
  border-radius: 2px;
  overflow: hidden;
}

.health-fill {
  height: 100%;
  background-color: #4caf50;
  transition: width 0.3s ease;
}

/* Selection Indicator */
.selection-indicator {
  position: absolute;
  top: -2px;
  left: -2px;
  right: -2px;
  bottom: -2px;
  border: 2px solid #61dafb;
  border-radius: 2px;
  pointer-events: none;
  animation: pulse 1s infinite;
}

@keyframes pulse {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.5; }
}

/* Viewport Info Overlay */
.viewport-info {
  position: absolute;
  top: 10px;
  left: 10px;
  background-color: rgba(0, 0, 0, 0.7);
  padding: 8px 12px;
  border-radius: 4px;
  display: flex;
  gap: 15px;
  font-size: 12px;
  color: #ccc;
  pointer-events: none;
  z-index: 100;
}

/* Page Info */
.page-info {
  background-color: #1e1e1e;
  padding: 10px 20px;
  border-top: 1px solid #444;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 14px;
  color: #ccc;
  gap: 20px;
}

.page-info span {
  color: #61dafb;
}

/* No Page Message */
.no-page-message {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  font-size: 18px;
  color: #888;
}

/* Responsive Design */
@media (max-width: 768px) {
  .canvas-main {
    flex-direction: column;
  }
  
  .sidebar {
    width: 100%;
    max-height: 200px;
    border-right: none;
    border-bottom: 2px solid #444;
  }
  
  .viewport {
    margin: 10px;
  }
  
  .controls {
    gap: 10px;
  }
  
  .controls button {
    padding: 6px 12px;
    font-size: 14px;
  }
}
